import React from 'react';
import AppBar from '@mui/material/AppBar'
import Toolbar from '@mui/material/Toolbar'
import { Box, IconButton } from '@mui/material';
import makeStyles from '@mui/styles/makeStyles';
import { Link } from 'react-router-dom';
import clsx from 'clsx';
import ProfileMenu from './ProfileMenu';
import {AlertMenu} from './AlertMenu';
import COC from '../../images/COC';
import HomeIcon from '@mui/icons-material/Home';//'@mui/icons-material/Home';

const useStyle = makeStyles(theme => {

  return ({
    menuButton: {
      marginRight: theme.spacing(2),
    },
    title: {
      flexGrow: 1
    },
    toolBar: {
      minWidth: theme.spacing(6),
      paddingLeft: 0,
    },
    appBar: {
      minWidth: theme.spacing(6),
      //backgroundColor: theme.palette.primary.main,
      zIndex: theme.zIndex.drawer + 1,
      transition: theme.transitions.create(['width', 'margin'], {
        easing: theme.transitions.easing.sharp,
        duration: theme.transitions.duration.leavingScreen,
      }),
    },
    appBarShift: {
      marginLeft: theme.drawerWidth,
      width: `calc(100% - ${theme.drawerWidth}px)`,
      transition: theme.transitions.create(['width', 'margin'], {
        easing: theme.transitions.easing.sharp,
        duration: theme.transitions.duration.enteringScreen,
      }),
    },
    hide: {
      display: 'none',
    },
    letrasCOC: {
      width: theme.spacing(10),
    },
    icon: {
      color: theme.palette.primary.contrastText,
      opacity: 0.8,
      minWidth: theme.spacing(4),
    },
  })
});

function NavBar(props) {

  const cssClases = useStyle();

  return (
    <AppBar position="fixed"
      className={clsx(cssClases.appBar, {
        [cssClases.appBarShift]: props.abrir,
      })}
    >
      <Toolbar className={cssClases.toolBar}>
        <Link to="/coc/panel/uci/home">
          <IconButton className={cssClases.icon} size="large">
            <HomeIcon fontSize="large"/>
          </IconButton>
        </Link>
        <Box className={cssClases.title}>
          <Box className={cssClases.letrasCOC}>
            <COC primary={true} />
          </Box>
        </Box>
        <AlertMenu />
        <ProfileMenu />
      </Toolbar>
    </AppBar>
  );

}

export default NavBar;
