import React, { useEffect } from "react"
import { ThemeProvider, StyledEngineProvider } from '@mui/material/styles';
import CssBaseline from '@mui/material/CssBaseline';

// import ContenedorP from "./panel/ContenedorP";
import Secured from "./Secured";

import { getTheme, getThemeValue, getColorTheme } from "../newTheme";
import { CommId, suscribe } from './tools/Comm2';
import { useSnackbar } from 'notistack';

import {
	BrowserRouter as Router,
	Switch,
	Route
} from 'react-router-dom';



const TOPIC = {
	DARK_THEME: '/theme/darkTheme',
	MSG_NOTISTACK: '/app/msg'
};


function Container() {

	const { enqueueSnackbar } = useSnackbar();
	const [darkState, setDarkState] = React.useState(getThemeValue());
	const [colorTheme, setColorTheme] = React.useState(getColorTheme());


	const changeTheme = (props) => {
		setDarkState(props.dark);
		setColorTheme(props.color);
	}

	const showMsg = (props) => {
		enqueueSnackbar(props.msg, props.options);
	}
	suscribe(CommId, TOPIC.DARK_THEME, changeTheme);
	suscribe(CommId, TOPIC.MSG_NOTISTACK, showMsg);

	useEffect(() => {
		document.cookie = "cocToken=notoken; path=/";
		document.cookie = "cocTokenType=notokentype; path=/";
	}, [])

	return (
		<StyledEngineProvider injectFirst>
			<ThemeProvider theme={getTheme(darkState, colorTheme)}>
				<CssBaseline />
				<Router>
					<Switch>
						<Route path="/coc/panel">
							{
								 <Secured/>
							}
							{
								// <ContenedorP />
							}
						</Route>
					</Switch>
				</Router>
			</ThemeProvider>
		</StyledEngineProvider>
	);
}

export default Container;
