import React, {useState} from 'react';
import { Box, Grid } from '@mui/material';
import Button from '@mui/material/Button';

import { resourceServer_backend } from '../../env' ;

import { Tabla } from '../helpers/Tabla';
import { styled } from '@mui/system';
import { Stl } from '../../Stl';

import { MediaQuery } from '../../MediaQuery' ;

// store
import { useDispatch } from 'react-redux' ;
import { setIntance } from '../../../../redux/neuroDeskSlice' ;
import { useSelector } from 'react-redux' ;
import { addToken } from '../helpers/token';

const { urlServer , postServer, getServer, putServer } = resourceServer_backend ;


export const actualizarFile = (file) => {

     putServer.body = JSON.stringify( file );

     const resq =  addToken(putServer) ;
     putServer.headers.append("Content-Type", "application/json");

     fetch(`${urlServer}?query=actualizar-instancia`, resq )
          .then( resp => {
               resp.text().then( obj => {
                    console.log( JSON.parse(obj) );
               })
          }).catch( err => console.log( err ) );
};

const loadParam = (obj) => {
     return({
          ...obj,
          name:obj.name,
          data: obj.status
                    ? obj.available
                         ? "Disponible"
                         : "Ocupada"
                    : "Inoperativa",
          url: obj.url
     });
};

export const Imagenes = () => {

     const classes = Stl();
     const { _xl, _lg } = MediaQuery() ;
     const dispatch = useDispatch();
     const instancia = useSelector( (state) => state.neuroDesk.value );
     

     const [ list, setList ] = useState({
          content: instancia === null ? [] : [loadParam(instancia)],
          visibility:'hidden',
          selected: instancia === null ? null : instancia.name.split('inst_')[1]-1
     }) ;
     const [url, setUrl] = useState({
          link:null
     });

     // useEffect( () => {
     //      console.log( JSON.parse( localStorage.getItem('token') ) )
     //      console.log( localStorage.getItem('token') ) 

     // });

     const Container = styled('div')({
          boxShadow: 'rgba(0, 0, 0, 0.4) 5px 5px 5px 3px',//boxShadow: 'rgb(80,80,255,40%) 4px 4px',
          visibility: list.visibility,
          border:'1px solid rgb(100,100,100,60%)',
          borderRadius:'3px',
          fontFamily:'Montserrat',
          paddingLeft:  '0.8rem',
          paddingTop:'0.3rem',
          color:'rgb(70,70,70)',
          position: 'absolute',
          width: _xl ? '1000px' : _lg ? '400px':'300px',
          background:'rgb(255,255,255,95%)',
          '&:hover':{
               zIndex:'1000',
          }
     });


     const sendFile = ( e ) => {

          if(list.selected === null) return ;

          const fileData = new FormData();

          for(let n in [...Array(e.target.files.length)] ){
               fileData.append(
                    "files",
                    e.target.files[n],
                    e.target.files[n].name
               );
          };

          postServer.body = fileData ;
          fetch(`${urlServer}?query=NeuroDesk-Files&nameVolumen=${list.content[list.selected].storage}`, addToken(postServer) );
     };

     
     const getInstancias = () => {

          if( list.visibility === 'visible'){
               return setList({...list,visibility:'hidden'})
          };

     

          fetch(`${urlServer}?query=instancias_neuroDesk`, addToken(getServer) )
               .then( resp => {
                    resp.text().then( obj => {

                         const data = JSON.parse( obj );

                         list.visibility = 'visible'  ;
                         list.content = [];

                         data.query.forEach( obj => {
                              list.content.push( loadParam(obj) )
                         })

                         setList({ ...list });
                    })
               });
     };

     const handleSelect = (e) => {
          const idx = list.content.findIndex( item =>
                              item.name === e.target.firstChild.data
                         ) ;

          if(!list.content[idx].available) return ; // se selecciona la misma instancia desplegada

          if( list.selected != null ){ // Liberar instancia cuando se cambia entre instancias
               list.content[list.selected].available = true ;
               delete list.content[list.selected].data  ;
               actualizarFile( list.content[list.selected] ) ;
          };

          list.content[idx].available = false ;
          list.visibility = 'hidden';
          list.selected = idx ;
          delete list.content[idx].data  ;
          dispatch( setIntance( {...list.content[idx]} ) );

          url.link = list.content[idx].url ;
          actualizarFile( list.content[idx] );

          setList({...list});
          setUrl({...url});
     };

     const liberarIntancia = () => {
          const idx = list.selected ;

          list.content[idx].available = true ;
          list.selected = null ;
          url.link = null ;
          delete list.content[idx].data  ;

          actualizarFile( list.content[idx] );

          setList({...list});
          setUrl({...url});
     };

     return(
          <Box
               className={classes.bg_white}
               style={{height: _xl?'950px': _lg? '805px' : '600px'}}
               my={3}
          >
               <Grid
                    container
                    spacing={1}
               >
                    <Grid xs={12} item ml={2} >
                         <Button
                              variant="contained"
                              onClick={ ()=>getInstancias() }
                              style={{marginRight:'0.5rem'}}
                         >
                              Instancias NeuroDesk
                         </Button>
                         <Container onClick={ e=>handleSelect(e) }>
                              <Tabla data={list.content}/>
                         </Container>

                         <Button
                              variant="contained"
                              component="label"
                              style={{marginRight:'0.5rem'}}
                         >
                              Cargar archivos
                              <input
                                   hidden
                                   type="file"
                                   
                                   multiple
                                   onChange={ (e)=>sendFile(e) }
                              />
                         </Button>

                         <Button
                              variant="contained"
                              onClick={ ()=>liberarIntancia() }
                              style={{marginRight:'0.5rem', visibility:list.selected === null ? 'hidden' : 'visible'}}
                         >
                              Liberar {  list.content[  list.selected  ]?.name }
                         </Button>
                    </Grid>

                    <Grid xs={12} item >
                         {
                              list.content[ list.selected ]?.url
                                   ?
                                   <iframe
                                        title="NeuroDesk"
                                        width="100%"
                                        height= {_xl?'950px': _lg? '805px' : '635px'}
                                        src={list.content[ list.selected ]?.url}
                                   ></iframe>
                                   :<h3 style={{padding:'1rem'}}> Sin instancia seleccionada</h3>
                         }
                    </Grid>
               </Grid>
          </Box>
     );

};


