import { createSlice } from '@reduxjs/toolkit'


const patientSlice = createSlice({
     name: 'idPatient',
     initialState:{value:'0'},
     reducers:{
          assignId: (state, id) =>{
               state.value = id.payload
          },
     }
});


export const { assignId } = patientSlice.actions ;

export default patientSlice ;
