import React from 'react';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableRow from '@mui/material/TableRow';
import Paper from '@mui/material/Paper';

import { styled } from '@mui/system';

export const Tabla = ({data, f}) => {

     const styleFont = {
          fontFamily:'Montserrat',
          fontSize: '0.9rem',
          width: '100%'
     };

     const  styleTable = {
          marginBottom : '0.8rem',
          width:'95%',
     };

     const Content = (props)=>{

          const Render = styled('div')({
               padding: '0.2rem',
               borderRadius: '0.2rem',
              '&:hover':{
                   background: props.enable === 'Disponible'
                                   ? '#b1b1ee'
                                   : '#fff',
                   cursor:  props.enable === 'Disponible'
                              ? 'pointer'
                              : 'auto'
              }
          });

          return(
               <Render>
                    {props.children}
               </Render>
          )
     }


     return(
          <TableContainer component={Paper} style={styleTable} >
               <Table  style={styleFont} >
                    <TableBody>
                         {
                              data
                                   ? data.map( (item,idx) => (
                                        <TableRow
                                             key={idx}
                                        >
                                             <TableCell
                                                  style={styleFont}
                                             >
                                                  <Content
                                                       enable={item.data}
                                                  >
                                                       {item.name}
                                                  </Content>
                                             </TableCell>

                                             <TableCell
                                                  style={styleFont}
                                                  align="right"
                                             >
                                                  {item.data}

                                             </TableCell>
                                        </TableRow>
                                   ))
                                   : <></>
                         }
                    </TableBody>
               </Table>
          </TableContainer>
     )
};
