


export const setToken = () => {
     
     localStorage.removeItem('token');

     if( document.cookie.split(';').length > 1 ){
          
          const token = document.cookie.split(';').filter( arr => arr.indexOf('cocToken=') !== -1 )[0].split('=')[1] ;

          localStorage.setItem('token', JSON.stringify( token ) ) ;
          
     }else{
          localStorage.setItem('token', JSON.stringify( document.cookie.split('=')[1].split(';')[0] ) ) ;
     };
     
};

export const addToken = ( obj, content = null ) => {
     
     setToken();
     
     const token = JSON.parse( localStorage.getItem('token') );

     obj.headers.delete('content-type');
     obj.headers.delete("authorization");
     obj.headers.append("authorization",`Bearer ${token}`)

     if( content ){
          obj.headers.append('content-type', "application/json");
     };

     return obj 
}
