import { createSlice } from '@reduxjs/toolkit' ;
import { actualizarFile } from '../components/uci/components/imagenes/Imagenes' ;


const neuroDeskSlice = createSlice({
     name: 'neuroDesk',
     initialState:{ value: null },
     reducers:{
          setIntance: (state, id) => {
               state.value = id.payload
          },
          clearIntance: (state, {payload}) => {
               actualizarFile(payload) ;
               state.value = null ;
          }
     }
});

export const { setIntance, clearIntance } = neuroDeskSlice.actions ;
export default neuroDeskSlice ;
