import React, { Suspense, Fragment, useEffect } from 'react';
import makeStyles from '@mui/styles/makeStyles';
import PanelBase from './PanelBase';
import clsx from 'clsx';
import NavBar from './NavBar';
import DrawerMenu from './DrawerMenu';
import { components } from './components';
// import SettingsDrawer from '../settings/SettingsDrawer';
import { v4 as uuid4 } from 'uuid' ;
import { Switch, Route } from 'react-router-dom';
// import ApplicationDrawer from '../settings/ApplicationDrawer';
import { getMenu } from '../../redux/config';
import { useDispatch } from 'react-redux';


const useStyle = makeStyles(theme => ({
	root: {
		display: 'flex'
	},
	offset: theme.mixins.toolbar,

	content: {
		flexGrow: 1,
		display: 'flex',
		flexDirection: 'column',
		padding: theme.spacing(3),
		overflowX: 'hidden',
		overflowY: 'auto',
		transition: theme.transitions.create('margin', {
			easing: theme.transitions.easing.sharp,
			duration: theme.transitions.duration.leavingScreen,
		}),
	},
	contentShift: {
		transition: theme.transitions.create('margin', {
			easing: theme.transitions.easing.easeOut,
			duration: theme.transitions.duration.enteringScreen,
		}),
	},
	backdrop: {
		zIndex: theme.zIndex.drawer + 1,
		color: '#fff',
	},
	errorMsg: {
		color: theme.palette.error.main,
	}

}))


const LoginS = () => {

	const classes = useStyle();
	const [abrir, setAbrir] = React.useState(false);
	const accionAbrir = () => {
		setAbrir(!abrir)
	}

	const dispatch = useDispatch();
	useEffect(() => {
		dispatch(getMenu())
	}, [dispatch])


	return (
		<Fragment>
			<NavBar
				accionAbrir={accionAbrir}
				abrir={abrir}
			/>
               {
			<DrawerMenu
				variant="persistent"
				open={abrir}
				accionAbrir={accionAbrir}
			/>
			// <SettingsDrawer />
			// <ApplicationDrawer />
               }
			<div
                    className={ clsx(classes.content, { [classes.contentShift]: abrir }) }
               >
				<div className={classes.offset}></div>
				<Suspense fallback={<div>Loading...</div>}>
					<Switch>
                              <Route exact path="/coc/panel" component={PanelBase} />
                              {
							components.map( item => <Route key={uuid4()} exact path={item.url} component={ item.comp } /> )
						}
					</Switch>
				</Suspense>
			</div>
		</Fragment>
	);

}



function ContenedorP(props) {

	return (
		<LoginS />
	);

}

export default ContenedorP;
