import menuInfo from './menuInfo.json';
// constants

const dataInicial = {
	menu: {
		group: [],
		items: {},
	},
	components: {
		ALERT_WEB: null
	}
}

const ADD_MENU = 'ADD_MENU'

// reducers

export default function configReducer(state = dataInicial, action) {
	switch (action.type) {
		case ADD_MENU:
    	return { ...state, menu: action.payload }
		default:
			return state
	}
}



// actions

export const getMenu = () => async (dispatch, getState) => {
	try {

		let menu = {
			group: [],
			items: {},
		};

		menuInfo.menuItems.forEach(grupo => {
			grupo.ITEMS.forEach(menuItem => {
				const item = window.keycloakInst.resourceAccess['web-ui']?.roles?.find(function (element) {
					return menuItem.ITEM_ID === element;
				});
				if (item) {
					if (!menu.items[grupo.GROUP_ID]) {
						menu.group.push(grupo);
						menu.items[grupo.GROUP_ID] = []
					}
					menu.items[grupo.GROUP_ID].push(menuItem);
				}
			})
		})

		dispatch({
			type: ADD_MENU,
			payload: menu
		})
	} catch (error) {
		console.log(error)
	}
}

