import React, {useState, useEffect} from 'react' ;
import AddAlertIcon from '@mui/icons-material/AddAlert' ;
import ReportRoundedIcon from '@mui/icons-material/ReportRounded';
import { styled } from '@mui/system' ;
import { MediaQuery } from '../uci/MediaQuery' ;
import {colorList} from '../../newTheme.js' ;

import { unitToTwoUnit } from '../uci/components/helpers/unitToTwoUnit';
import { getLastHour } from '..//uci/components/helpers/getLastHour';
import { resourceServer } from '../uci/env'
import { prom } from '../uci/components/dashboard/helpers/prom';

//Redux
import { useSelector } from 'react-redux' ;

const colors = colorList;

const BoxAlert = ({alerts}) => {

     const [display, setDisplay] = useState((state)=>'none');
     const { _xl, _lg } = MediaQuery() ;

     const RenderBox = styled('div')({
          position:'absolute',
          right:'1rem',
          border:'0.2rem solid rgb(30,30,200,1)',
          borderRadius:'0.4rem',
          display: display,
          background:'white',
          width: 'max-content'
     });

     const Item = styled('p')({
          paddingBlock:'0.3rem',
          fontSize: _xl ? '25px' : _lg ? '20px': '14px',
          fontWeight: '500',
          fontFamily: 'Montserrat !important',
          color: colors.color1.main,
          paddingInline:'1rem',
     });

     const Title= styled('p')({
          margin:0,
          paddingBlock:'0.2rem',
          fontFamily: 'Montserrat !important',
          fontWeight:'600',
          fontSize: _xl ? '28px' : _lg ? '24px': '16px',
          paddingLeft:'0.5rem',
          color: colors.color1.main,
          background:colors.color1.main,
          justifyContent:'left'
     });

     return([
          () => setDisplay( (state) => state === 'none' ? 'block' : 'none' ) ,
          () =>
               <RenderBox>
                    {alerts.map( (alert, idx) =>
                         <div key={idx}>
                              <Title>
                                   Alertas
                              </Title>
                              <Item key={idx}>
                                   <strong> {alert.type} </strong> {alert.num !== 0 ? `${alert.num} Alertas` : ''}
                              </Item>
                         </div>

                    )}
               </RenderBox>
     ])
} ;

const { urlServer, requestOptionsGet } = resourceServer ;


export const AlertRespirador = async(id, dayNow, setAlerts=()=>{}, set=true ) => {

     if( id === '0' ){
          setAlerts([
                    {
                         type:'Seleccione un paciente',
                         num:0
                    }
               ])
          return
     }

     const dte = `${dayNow[2]}-${unitToTwoUnit(dayNow[1])}-${unitToTwoUnit(dayNow[0])}` ;// 2022-08-10
     const hour =  getLastHour( 'lastHour' ) ;

     const findOutvalue = (interval, array) => {

          let low = interval.split('-')[0] ;
          let hight = interval.split('-')[1] ;

          const mean = prom( array.map(n => n[0]) );

          let flag = (
               array.findIndex( num => ( num[0] === 0 ? mean : num[0] ) <= parseFloat(low) ) >= 0
               ||
               array.findIndex( num => ( num[0] === 0 ? mean : num[0] ) >= parseFloat(hight) ) >= 0
          );
          return( flag );
     };

     const numAlerts = fetch(`${urlServer}Observation?identifier=${dte}${hour}&identifier=VentSupport_epochs_${id}&subject=${id}`, requestOptionsGet)
               .then( resp =>
                    resp.text().then( obj => {

                         const data = JSON.parse( obj );

                         let count = 0 ;

                         if( data.total > 0 ){

                              let component = data.entry.find( entry => entry.resource.code.text === 'metricas').resource.component ;

                              component.filter( obj =>
                                   [
                                        'diagnosis', 'age','height',
                                        'weight', 'gender','PBW',
                                        'BMI','assessment','comment_out'
                                   ].map( item => item !== obj.code.text ).findIndex( flag => flag === false ) < 0  )
                                   .forEach( obj => {

                                        if( obj.code?.coding ){
                                             // let { text, coding } = obj.code ;
                                             let { coding } = obj.code ;

                                             let { valueString } = obj ;

                                             let value = JSON.parse( valueString ) ;

                                             let interval = coding[4].display.split(':')[1];

                                             count = count + (findOutvalue( interval, value) ? 1 : 0)

                                        };

                              });

                              if( set ){
                                   setAlerts( (state) => {
                                        if( state.length > 1 ){
                                             return([ ...state,
                                                  {
                                                       type:'Respiratorio:',
                                                       num:count
                                                  }
                                             ])
                                        }

                                        return([
                                             {
                                                  type:'Respiratorio:',
                                                  num:count
                                             }
                                        ])

                                   });
                              };
                         };
                         return( count );
                    })
               )

          return( numAlerts );
};

export const AlertMenu = () => {

     const id = useSelector( (state) => state.patiendId.value );
     const dayNow = useSelector( (state)=> state.dayNow.value );


     const [alerts, setAlerts] = useState( () => [
          {
               type:'Selecccione un paciente',
               num:0
          }
     ]);

     useEffect( ()=>{
          AlertRespirador( id, dayNow, setAlerts );
     },[ id, dayNow ]);

     const [setDisplay, RenderAlert] = BoxAlert({alerts});

     const flagAlert = alerts.findIndex( alert => alert.num > 0) >= 0 ;

     const styleIcon = {
          opacity:'0.8',
          cursor:'pointer'
     };

     const styleIconDanger = {
          opacity:'0.85',
          color: flagAlert ? 'red' : 'transparent',
          position: 'relative',
          left: '-1rem',
          top: '-0.4rem'
     }


     return(
          <div>
               <AddAlertIcon
                    fontSize="large"
                    style={styleIcon}
                    onClick={setDisplay}
               />
               <ReportRoundedIcon
                    style={styleIconDanger}
                    fontSize="large"
                    onClick={setDisplay}
               />

               <RenderAlert />
          </div>
     )
};
