import { lazy } from 'react';

const Monitor = lazy(() => import('../uci/Monitor'));
const Formulario = lazy(() => import('../uciTablet/Formulario'));
const Home = lazy(() => import('../uci/Home'));
const CreatePatient = lazy( ()=> import('../uci/components/createPatient/CreatePatient'));

const preUrl = "/coc/panel";
export const components = [
     {url: preUrl + "/uci/home", comp: Home},
     {url: preUrl + "/uci/monitor", comp: Monitor},
     {url: preUrl + "/uci/formularioEnfermeria", comp: Formulario},
     {url: preUrl + "/uci/crearPaciente", comp: CreatePatient},
];
