import {
  IconButton,
  Menu,
  MenuItem,
  Typography,
  Divider,
  ListItemIcon,
  ListItemText,
} from '@mui/material';
import makeStyles from '@mui/styles/makeStyles';
import withStyles from '@mui/styles/withStyles';
import React, { useState } from 'react';
import AccountCircleIcon from '@mui/icons-material/AccountCircle';
import ExitToAppIcon from '@mui/icons-material/ExitToApp';
import SettingsIcon from '@mui/icons-material/Settings';
import { publish } from '../tools/Comm2';

const TOPIC = {
  SETTING_DISPLAY: '/settings/display',
  APP_DRAWER_DISPLAY: '/applicationDrawer/display',
};

const useStyle = makeStyles(theme => {
  return ({
    title: {
      //backgroundColor: theme.palette.action.selected,
      backgroundColor: theme.palette.primary.light,
      color: theme.palette.primary.contrastText,
      padding: theme.spacing(0.5),
      display: 'block',
      textTransform: 'none',
      //opacity: 0.7,
      // lineHeight: theme.spacing(0.5),
    },
    icon: {
      color: "inherit",
      opacity: 0.8,
      minWidth: theme.spacing(4),
    },
    menuPaper: {
      borderRadius: theme.shape.borderRadius,
      borderWidth: 2,
      borderStyle: 'solid',
      borderColor: theme.palette.primary.light,

    },
    menuList: {
      padding: 0
    },
    linkClass: {
      textDecoration: 'none',
      color: 'inherit',
      cursor: 'inherit',
      '&:hover': {
        textDecoration: 'none',
      },
    },
  })
});

const StyledMenuItem = withStyles((theme) => ({
  root: {
    paddingTop: theme.spacing(0.5),
    paddingBottom: theme.spacing(0.5),
    '&:hover': {
      backgroundColor: theme.palette.primary.main,
      color: theme.palette.primary.contrastText,
      //'& .MuiListItemIcon-root, & .MuiListItemText-primary': {
      //color: theme.palette.common.white,
      //  color: theme.palette.primary.contrastText,
      //},
    },
  }
}))(MenuItem);


const ProfileMenu = () => {
  const [anchorEl, setAnchorEl] = useState(null);
  const classes = useStyle();

  const handleClose = (event) => {
    setAnchorEl(null);
  }
  const handleOpen = (event) => {
    setAnchorEl(event.currentTarget);
  }
  const logOutClick = async (event) => {
    console.log(window.keycloakInst);
    document.cookie = "cocToken=; expires=Thu, 01 Jan 1970 00:00:00 UTC; path=/;";
    document.cookie = "cocTokenType=; expires=Thu, 01 Jan 1970 00:00:00 UTC; path=/;";
    window.keycloakInst.logout();
    window.location.replace("/coc/panel");
  }
  const openSettings = () => {
    setAnchorEl(null);
    publish(TOPIC.SETTING_DISPLAY, { display: true });
  };
  const openAppDrawer = () => {
    setAnchorEl(null);
    publish(TOPIC.APP_DRAWER_DISPLAY, { display: true });
  }
  return (
    <div>

      <IconButton
        className={classes.icon}
        aria-label="menu profile"
        onClick={handleOpen}
        size="large">
        <AccountCircleIcon fontSize="large" />
      </IconButton>
      <Menu classes={{
        paper: classes.menuPaper,
        list: classes.menuList,
      }}
        dense={1}
        elevation={0}
        anchorOrigin={{
          vertical: 'bottom',
          horizontal: 'center',
        }}
        transformOrigin={{
          vertical: 'top',
          horizontal: 'center',
        }}
        id="customized-menu"
        anchorEl={anchorEl}
        keepMounted
        open={Boolean(anchorEl)}
        onClose={handleClose}
      >
        <Typography className={classes.title} variant="button">Ajustes</Typography>
        <StyledMenuItem onClick={openSettings}>
          <ListItemIcon className={classes.icon} >
            <SettingsIcon fontSize="small" />
          </ListItemIcon>
          <ListItemText primary="Ajustes" /></StyledMenuItem>
        <Typography className={classes.title} variant="button">Aplicación</Typography>
        <StyledMenuItem onClick={openAppDrawer}>
          <ListItemIcon className={classes.icon} >
            <SettingsIcon fontSize="small" />
          </ListItemIcon>
          <ListItemText primary="Logs" /></StyledMenuItem>
        <Divider />


        <StyledMenuItem name="salirB" onClick={logOutClick}>
          <ListItemIcon className={classes.icon} >
            <ExitToAppIcon fontSize="small" />
          </ListItemIcon>
          <ListItemText primary="Salir" />
        </StyledMenuItem>
      </Menu>
    </div>
  );
};

export default ProfileMenu;