
import { v4 as uuidv4 } from 'uuid';

let fun = {};

export const CommId = uuidv4();

export const suscribe = (id, topicName, service) => {
	
	if (!fun[topicName]) {
		fun[topicName] = {
			ids: [],
			function: {}
		};
	}
	
	if (!fun[topicName].function[id]) {
		fun[topicName].ids.push(id);
		fun[topicName].function[id] = {};
	}
	fun[topicName].function[id] = service;

};



export const publish = (topicName, props={}) => {
	if (fun[topicName]) {
		fun[topicName].ids.forEach(id => {
			fun[topicName].function[id](props);
		});
	}
	
};


export const print = (props={}) => {
	console.log('Suscripciones:', fun)
	
};

