

export const getLastHour = ( last='date', ymd=false ) => {

     const d = new Date().toLocaleString("en-GB", {timeZone: "Atlantic/Azores"}).split('/') ;

     const day = d[0] ;
     const month = d[1] ;
     const [year, hour] = d[2].split(', ') ;

     if( last === 'date' ){ // Tiempo actual
          return( ymd ? `${year}-${month}-${day}T${hour}Z` : `T${hour}Z` );
     };

     if( last === 'lastHour' ){ // Tiempo actual con la hora en ceros
          return( ymd ? `${year}-${month}-${day}T${hour.split(':')[0]}:00:00Z` : `T${hour.split(':')[0]}:00:00Z` );
     };

     if( last === 'ZeroHour' ){ // Fecha actual a media noche
          return( ymd ? `${year}-${month}-${day}T00:00:00Z` : `T00:00:00Z` );
     }
};
