import { createSlice } from '@reduxjs/toolkit' ;

const dayNowSlice = createSlice({
     name: 'dayNow',
     initialState:{value:[0,0,0]},
     reducers:{
          assignDay: (state, day)=>{
               state.value = day.payload
          }
     },
});

export const { assignDay } = dayNowSlice.actions ;

export default dayNowSlice ;
