
import { createTheme } from '@mui/material/styles';
import {blue, orange, red, green} from '@mui/material/colors';
import { publish } from './components/tools/Comm2';

const TOPIC = {
	DARK_THEME: '/theme/darkTheme'
};

export const baseColor = {
	color1: false,
	color2: false,
	color3: false,
	color4: false,
}
let vColor = {};

export const colorList = {
	color1: {main: blue[900]},
	color2: {main: red[900]},
	color3: {main: green[900]},
	color4: {main: orange[800]},
}

const cookieTheme = "cocDarktheme";
const cookieColor = "cocColorTheme";

let selectedColor = {main: blue[900]}; // color por defecto
let selectedColorName = 'color1';
let darkState = false;

let palletType = "";
let paperGrafana = "";
let backgroundColor = "";


export const setDark = (cvalue) => {
	document.cookie = cookieTheme + "=" + cvalue + "; path=/coc/";
	darkState = cvalue;
	updateParms();
}



const getCookieTheme = () => {

	let decodedCookie = decodeURIComponent(document.cookie);
	let ca = decodedCookie.split(';');
	for (let i = 0; i < ca.length; i++) {
		let c = ca[i];
		while (c.charAt(0) === ' ') {
			c = c.substring(1);
		}
		if (c.indexOf(cookieTheme) === 0) {
			const value = c.substring(cookieTheme.length+1, c.length);
			if (value === 'true') {
				return true;
			} else {
				return false;
			}
		}
	}
	return false;
}

const getCookieColorTheme = () => {

	let decodedCookie = decodeURIComponent(document.cookie);
	let ca = decodedCookie.split(';');
	for (let i = 0; i < ca.length; i++) {
		let c = ca[i];
		while (c.charAt(0) === ' ') {
			c = c.substring(1);
		}
		if (c.indexOf(cookieColor) === 0) {
			const value = c.substring(cookieColor.length+1, c.length);
			return value;
		}
	}
	return 'color1';
}

//https://material.io/resources/color/#!/?view.left=0&view.right=0&primary.color=C62828&primary.text.color=ffffff
const updateParms = () => {
	darkState = getCookieTheme();
	selectedColorName = getCookieColorTheme();
	selectedColor = colorList[selectedColorName];
	vColor = {...baseColor, [selectedColorName]: true}
  palletType = darkState ? "dark" : "light";
	paperGrafana = darkState ? "#1a1c20" : "#fff";
	backgroundColor = darkState ? "#303030" : "#fff";
	publish(TOPIC.DARK_THEME,{
		dark: darkState,
		color: selectedColorName
	});
}

updateParms();


export let getTheme = ()=>{
	return createTheme({
		palette: {
			mode: palletType,
			primary: selectedColor,
			paperGrafana: paperGrafana,
			background: {default: backgroundColor},
			//secondary: {main:'#1565c0',light:'#5e92f3',dark:'#003c8f'}
		},
		typography: {
			fontSize: 12
		},
		drawerWidth: 56
	});
}

export let getThemePrinter = ()=>{
	return createTheme({
		palette: {
			mode: "light",
			primary: selectedColor,

			//secondary: {main:'#1565c0',light:'#5e92f3',dark:'#003c8f'}
		},
		drawerWidth: 240
	});
}


export const getThemeValue = () => {
	return darkState;
}

export const getColorList = () => {
	return vColor;
}

export const getColorTheme = () => {
	return selectedColorName;
}

export const changeTheme = () => {
	setDark(!darkState)
}
export const changeColorTheme = (color) => {
	document.cookie = cookieColor + "=" + color + "; path=/coc/";
	selectedColorName = color;
	updateParms();
}
