import React, { Component } from 'react';
import ContenedorP from "./panel/ContenedorP";
import {setToken} from './uci/components/helpers/token';


class Secured extends Component {

  constructor(props) {
    super(props);
    this.state = { keycloak: null, authenticated: false };
  }

  componentDidMount() {
    window.keycloakInst = window.Keycloak('/coc/public/keycloak.json');


    window.keycloakInst.onAuthSuccess = function() { console.log('Keycloak authenticated - 1'); }

    window.keycloakInst.onTokenExpired = function() { 
      console.log('Keycloak onTokenExpired - 1'); 
      window.keycloakInst.updateToken(120).success((refreshed)=>{
        if (refreshed){
            console.log('Keycloak refreshed '+new Date());
            setToken();
            document.cookie = "cocToken="+window.keycloakInst.token+"; expires="+window.keycloakInst.tokenParsed.exp+"; path=/";
            document.cookie = "cocTokenType="+window.keycloakInst.tokenParsed.typ+"; expires="+window.keycloakInst.tokenParsed.exp+"; path=/";
        }else {
            console.log('not refreshed '+new Date());
        }
      }).error((err) => {
          console.error('Failed to refresh token '+new Date()+' -- '+err);
      });
    }

    window.keycloakInst.init({onLoad: 'login-required'}).then(authenticated => {
      document.cookie = "cocToken="+window.keycloakInst.token+"; expires="+window.keycloakInst.tokenParsed.exp+"; path=/";
      document.cookie = "cocTokenType="+window.keycloakInst.tokenParsed.typ+"; expires="+window.keycloakInst.tokenParsed.exp+"; path=/";

      this.setState({ keycloak: window.keycloakInst, authenticated: authenticated })

    })
    

  }

  render() {
    if (this.state.keycloak) {
      if (this.state.authenticated){
        return (
          <ContenedorP/>
        );
      }else{
        return (<div>Unable to authenticate!</div>);
      }
    }
    return (
      <div>Initializing Keycloak...</div>
    );
  }

}
export default Secured;
